.App-audio {
  display: flex;
  justify-content: space-between;
  align-items: center;
}



h1 {
  margin: 0 0;
  font-size: 1.2em;
  color: #14b8a6;
  text-transform: uppercase;

}

h2 {
  margin: 0 0;
  font-size: 1em;
  color: #0284c7;
  text-transform: uppercase;
}

p {
  margin: 0 0 1em 0;
}

h4 {
  color: #0284c7;
  text-transform: uppercase;
  margin: 0.2em 0;
}

.App-container {
  display: flex;
  flex-direction: row
}

.App-box {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  overflow-y: auto;
  max-width: 30%;
}

@media (max-width: 945px) {
  .App-container {
    display: flex;
    flex-direction: column
  }

  .App-box {
    max-width: 100%
  }

  .App-description {
    flex: 1 2 auto;
  }
}